.progress
{
    position: absolute;
    


    width: 19%;
    height: 90%;
    
  left: 50%;


  top: 32%;
    transform: translate(-50%, -50%);


    border: 1px solid #ffffff;
    border-radius: 5%;
    padding: 5px;
    
}


.circle{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-clip: padding-box;
    width: 27vw;
    height: 30vw;
    border: 1.5vw solid rgba(0,0,0,0);
    box-shadow: 0 0 0 2px rgba(255,255,255,0.4);
    border-radius: 5%;
    overflow: hidden;
   
}
.wave{
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 5%;
}

.wave:before,
.wave::after
{
    content: '';
    position: absolute;
    width: 200%;
    height: 200%;
    top: 0;
    left: 50%;
    transform: translate(-50%, -75%); /* translate(-50%, -75%) */
    background: #000;
}
.wave:before
{
    border-radius: 0%; /* 45 */
    background: rgba(255,255,255,.5);
    animation: animate 5s linear infinite;
    transform: translate(50%, 50%);
}
.wave:after
{
    border-radius: 0%; /* 45 */
    background: rgba(255,255,255,.5);
    animation: animate 10s linear infinite;
    transform: translate(50%, 50%);
    
}
@keyframes animate
{
    0%
    {
        transform: translate(-50%,-75%) rotate(0deg)
    }
    100%
    {
        transform: translate(-50%,-75%) rotate(360deg)
    }
}
